import React, { useState, useEffect } from 'react';
import gets from '../db/gets.json';
import { getAxiosConnect, replaceUrls, replaceUrls2, getArticlesId, updateArticles, initalBaseUrl, getFiles, array_obj, getMenu, getMenuAdminId, getArticles, getMenuArtId, deleteMenuArt, insertMenuArt } from '../actions';
import { Link } from 'react-router-dom';
import Artyesno from '../components/Artyesno';
function Articlesupdate(props) {
    const [menuname, setmenuname] = useState({ data: [{}], status: 0 });
    const [art, setart] = useState({ data: [{}], status: 0 });
    const [files, setfiles] = useState({ data: {}, status: 0 });
    const [artping, setartping] = useState({ data: [{}], status: 0 });
    const [articles, setarticles] = useState({ data: {}, status: 0 });
    const [articlesid, setarticlesid] = useState({ data: [{}], status: 0 });
    const [id, setid] = useState(0);
    const [names, setnames] = useState('');
    const [alias, setalias] = useState('');
    const [title, settitle] = useState('');
    const [keyword, setkeyword] = useState('');
    const [descript, setdescript] = useState('');
    const [content, setcontent] = useState('');
    const [subcontent, setsubcontent] = useState('');
    const [img, setimg] = useState({ img: '', bol: false });
    const [params, setparams] = useState(0);
    
    
    

    useEffect(() => {
        setalias(replaceUrls(names));
    }, [names]);

   

    useEffect(() => {
        getAxiosConnect(setart, gets.getbaseurl + gets.getArticlesId, {
            params: {
                art_id: props.match.params.id
            }
        });
        getAxiosConnect(setarticlesid, gets.getbaseurl + gets.getMenuArtId, {
            params: {
                menu_id: props.match.params.id
            }
        });
    }, [props.match.params.id]);
    useEffect(() => {
        getAxiosConnect(setfiles, gets.getbaseurl + gets.getFiles);
        getAxiosConnect(setmenuname, gets.getbaseurl + gets.getMenu);
        getAxiosConnect(setarticles, gets.getbaseurl + gets.getArticles);
    }, []);

    useEffect(() => {
        setnames(art.data.art_name);
        // setalias(art.data.art_alias);
        settitle(art.data.art_title);
        setkeyword(art.data.art_keyword);
        setdescript(art.data.art_descript);
        setsubcontent(art.data.art_subcontent);
        setcontent(art.data.art_content);
        setparams(art.data.params);
        setid(art.data.art_id);
        setimg({ img: art.data.art_img, bol: false });

    }, [
        art.data.art_name,
        // art.data.art_alias,
        art.data.art_title,
        art.data.art_keyword,
        art.data.art_descript,
        art.data.art_subcontent,
        art.data.art_content,
        art.data.params,
        art.data.art_id,
        art.data.art_img
    ]);

    function handleSubmit(event) {


        getAxiosConnect(setartping, gets.getbaseurl + gets.updateArticles, {
            params: {
                art_img: img.img,
                art_name: names,
                art_alias: alias,
                art_title: title,
                art_keyword: keyword,
                art_descript: descript,
                art_subcontent: subcontent,
                art_content: content,
                params: params,
                art_id: id
            }
        });

        event.preventDefault();


    }

    return (
        <div className="container-fluid" >
            <h2>Создание нового пункта меню</h2>
            <form onSubmit={handleSubmit}>
                <div className="row">
                    <div className="row">
                        <button className="btn btn-primary ml-4 " type="submit">Сохранить</button>
                        <Link className="btn btn-primary ml-3" to={`/admin/index.php/articles/${art.data.art_id}`}>Закрыть</Link>
                    </div>

                    <Artyesno setparams={setparams} params={params} />
                </div>

                <div className="row">
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_name"><h3>Название *</h3></label>
                            <input defaultValue={art.data.art_name} type="text" className="form-control" id="menu_name" onChange={(e) => setnames(e.target.value)} />
                        </div>
                    </div>

                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Адрес страницы *</h3></label>
                            <input type="text" defaultValue={alias} onChange={(e) => setalias(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Заголовок страницы *</h3></label>
                            <input type="text" defaultValue={art.data.art_title} onChange={(e) => settitle(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm">
                        <label htmlFor="menu_keyword"><h3>Ключевые слова</h3></label>
                        <textarea className="form-control" id="menu_keyword" onChange={(e) => setkeyword(e.target.value)} rows={3} defaultValue={art.data.art_keyword} />
                        <label htmlFor="menu_descript"><h3>Краткое описание страницы</h3></label>
                        <textarea className="form-control" id="menu_descript" onChange={(e) => setdescript(e.target.value)} rows={3} defaultValue={art.data.art_descript} />
                        <label htmlFor="cub_content"><h3>Краткое описание статьи</h3></label>
                        <textarea className="form-control" id="cub_content" onChange={(e) => setsubcontent(e.target.value)} rows={3} defaultValue={art.data.art_subcontent} />
                        <label htmlFor="content"><h3>Текст статьи</h3></label>
                        <textarea className="form-control" id="content" onChange={(e) => setcontent(e.target.value)} rows={8} defaultValue={art.data.art_content} />

                        <h3 className="mt-4">Изображение</h3>
                        <div className="col-sm-4"><img width="100%" src={`${initalBaseUrl}/img/window/${art.data.art_img}`} alt="Изображение" /></div>
                        <div className="row">
                            {array_obj(files.data).map((x, i) =>
                                <div key={i + 10} style={{ border: 'solid 2px green' }} style={(img.img === x) ? { border: 'solid 2px red' } : { border: 'solid 2px green' }} className="form-check m-2  col-sm-1 " >
                                    <input style={{ marginLeft: '-1rem' }} id={`menu_img_${i}`} className="form-check-input" type="checkbox" value={(img.bol === false) ? x : ''} onChange={(e) => setimg({ img: e.target.value, bol: e.target.checked })} />
                                    <label htmlFor={`menu_img_${i}`} className="form-check-label"><img width="100%" src={`${initalBaseUrl}/img/window/${x}`} /></label>
                                </div>
                            )}


                        </div>
                    </div>

                </div>


                <div>

                </div>
            </form>
        </div>
    )
}
export default Articlesupdate;