import React from 'react';
import ChecedsClick from './ChecedsClick';
import '../css/images.css';
function Images(props) {
    return (
        <div>
            <h3>Изображение</h3>
            <div className="row"> {Object.values(props.images).map((x, i) => <div onClick={() => { props.getarrcheceds(props.arrcheceds, i + 1, { b: true, k: props.arrcheceds }) }} className="col-2 text-center img" key={i + 245} ><ChecedsClick c={props.arrcheceds[props.arrcheceds.indexOf(i + 1)]} /><img  src={`/img/window/${x}`}/></div>)}</div>
        </div>
    )
} export default Images;