import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { config } from '../redux/actions';
import menu from '../db/menu.json'
function Menu(props) {
    useEffect(() => {
        props.getconfigMenu();
    }, []);
    return (
        <nav className="nav flex-column" style={{ width: '200px' }}>


            {menu.map((x) =>
                <li className="nav-item">
                    <Link className="nav-link active" to={x.alias}>
                        {(x.icon === 'home') ?
                            <svg className="bi bi-house-fill m-1" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path fillRule="evenodd" d="M8 3.293l6 6V13.5a1.5 1.5 0 01-1.5 1.5h-9A1.5 1.5 0 012 13.5V9.293l6-6zm5-.793V6l-2-2V2.5a.5.5 0 01.5-.5h1a.5.5 0 01.5.5z" clipRule="evenodd" />
                                <path fillRule="evenodd" d="M7.293 1.5a1 1 0 011.414 0l6.647 6.646a.5.5 0 01-.708.708L8 2.207 1.354 8.854a.5.5 0 11-.708-.708L7.293 1.5z" clipRule="evenodd" />
                            </svg> :
                            (x.icon === 'menu') ?
                                <svg className="bi bi-justify m-1" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                    <path fillRule="evenodd" d="M2 12.5a.5.5 0 01.5-.5h11a.5.5 0 010 1h-11a.5.5 0 01-.5-.5zm0-3a.5.5 0 01.5-.5h11a.5.5 0 010 1h-11a.5.5 0 01-.5-.5zm0-3a.5.5 0 01.5-.5h11a.5.5 0 010 1h-11a.5.5 0 01-.5-.5zm0-3a.5.5 0 01.5-.5h11a.5.5 0 010 1h-11a.5.5 0 01-.5-.5z" clipRule="evenodd" />
                                </svg> :
                                ''
                        }
                        {x.name}
                    </Link>
                </li>
            )}
        </nav>

    )
}

const setPath = state => ({
    menu: state.configMenu
})

const toProps = dispatch => ({
    getconfigMenu: () => dispatch(config()),

})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Menu);
