import React, { useState, useEffect } from 'react';
import gets from '../db/gets.json';
import { getAxiosConnect, replaceUrls, getFiles, array_obj, getArticles } from '../actions';
import { Link } from 'react-router-dom';
import Articles from '../components/Articles';
import Updateinput from '../components/Updateinput';
import '../css/menuupdate.css'
import { connect } from 'react-redux';
import { menu_a, menuid_a, menuartid_a, art_a, images_a } from '../redux/actions';
import Menutether from '../components/Menutether';
import Images from '../components/Images';
function Menuupdate(props) {
    useEffect(() => {
        props.getmenu();
        props.getarticles();
        props.getimages();
    }, []);
    useEffect(() => {
        props.getmenuid(props.match.params.id);
        props.getmenuartid(props.match.params.id);
    }, [props.match.params.id]);
    function handleSubmit(event) {

        event.preventDefault();
    }
    return (
        <div className="container-fluid" >
            <div className="row">
                <div className="col">
                    <h2>Редактировать пункт меню</h2>
                </div>
            </div>
            <form onSubmit={handleSubmit}>
                <div className="row">
                    <div className="row">
                        <button className="btn btn-primary ml-4 " type="submit">Сохранить</button>
                        <Link className="btn btn-primary ml-3" to={`/admin/index.php/menu/1`}>Закрыть</Link>
                    </div>
                </div>
                <div className="row">
                    <Updateinput />
                    <div className="col-5">
                        <Menutether menu={props.menu} menuid={props.menuid} />
                        <Articles menuartid={props.menuartid} articles={props.articles} arrcheceds={props.arrcheceds} getarrcheceds={props.getarrcheceds} />
                    </div>
                </div>
                <div className="col">
                    <Images images={props.images} arrcheceds={props.arrchecedsimg} getarrcheceds={props.getarrchecedsimg} />
                </div>

            </form>
        </div>
    )
}
const setPath = state => ({
    menu: state.menu,
    arrcheceds: state.arrcheceds,
    menuid: state.menuid,
    menuartid: state.menuartid,
    articles: state.articles,
    inputnames: state.inputnames,
    inputalias: state.inputalias,
    inputtitle: state.inputtitle,
    inputkeyword: state.inputkeyword,
    inputdescript: state.inputdescript,
    images: state.images,
    arrchecedsimg: state.arrchecedsimg
})

const toProps = dispatch => ({
    getmenu: () => dispatch(menu_a()),
    getmenuid: (id) => dispatch(menuid_a(id)),
    getmenuartid: (id) => dispatch(menuartid_a(id)),
    getarticles: () => dispatch(art_a()),
    getarrcheceds: (ars, e, f) => dispatch({ type: 'ARRCHECEDS', ars: ars, e: e, f: f }),
    getarrchecedsimg: (ars, e, f) => dispatch({ type: 'ARRCHECEDSIMG', ars: ars, e: e, f: f }),
    getimages: () => dispatch(images_a())
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Menuupdate);