import React from 'react';
import { NavLink } from 'react-router-dom';
import ChecedsClick from '../components/ChecedsClick';
function List(props) {

    return (

        <tr>
            <td className="text-center col-1" onClick={() => { props.getarrcheceds(props.arrcheceds, props.id, { b: true, k: props.arrcheceds })}}><ChecedsClick c={props.arrcheceds[props.arrcheceds.indexOf(props.id)]} /> </td>
            <td><NavLink className="adminl" activeStyle={{ color: 'green' }} to={props.lin}>{props.name}</NavLink></td>
        </tr>
    )
} export default List;