import React, {useEffect } from 'react';
import gets from '../db/gets.json';
import { connect } from 'react-redux';
import { menu_a, menuid_a, replaceUrls } from '../redux/actions';
const Ubdateinput = (props) => {
    useEffect(() => {
        props.getinputnames({ type: 'INPUTNAMES', payload: props.menuid.menu_name });
        props.getinputalias({ type: 'INPUTALIAS', payload: replaceUrls(props.menuid.menu_name) });
        props.getinputtitle({ type: 'INPUTTITLE', payload: props.menuid.menu_title });
        props.getinputkeyword({ type: 'INPUTKEYWORD', payload: props.menuid.menu_keyword });
        props.getinputdescript({ type: 'INPUTDESCRIPT', payload: props.menuid.menu_keyword });
    }, [props.menuid]);
    return (
        <div className="col">
            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_name"><h3>Название *</h3></label>
                    <input defaultValue={props.menuid.menu_name} type="text" className="form-control" id="menu_name" onChange={(e) => { props.getinputnames({ type: 'INPUTNAMES', payload: e.target.value }); props.getinputalias({ type: 'INPUTALIAS', payload: replaceUrls(e.target.value) }) }} />
                </div>
            </div>

            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_alias"><h3>Адрес страницы *</h3></label>
                    <input type="text" defaultValue={props.inputalias} onChange={(e) => props.getinputalias({ type: 'INPUTALIAS', payload: replaceUrls(e.target.value) })} className="form-control" id="menu_alias" />
                </div>
            </div>
            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_alias"><h3>Заголовок страницы *</h3></label>
                    <input type="text" defaultValue={props.menuid.menu_title} onChange={(e) => props.getinputtitle({ type: 'INPUTTITLE', payload: e.target.value })} className="form-control" id="menu_alias" />
                </div>
            </div>


            <div className="col-sm">
                <label htmlFor="menu_keyword"><h3>Ключевые слова</h3></label>
                <textarea className="form-control" id="menu_keyword" onChange={(e) => props.getinputkeyword({ type: 'INPUTKEYWORD', payload: e.target.value })} rows={3} defaultValue={props.menuid.menu_keyword} />
                <label htmlFor="menu_descript"><h3>Краткое описание</h3></label>
                <textarea className="form-control" id="menu_descript" onChange={(e) => props.getinputdescript({ type: 'INPUTDESCRIPT', payload: e.target.value })} rows={3} defaultValue={props.menuid.menu_descript} />
            </div>
        </div>)
}


const setPath = state => ({
    menuid: state.menuid,
    inputnames: state.inputnames,
    inputalias: state.inputalias,
    inputtitle: state.inputtitle,
    inputkeyword: state.inputkeyword,
    inputdescript: state.inputdescript
})

const toProps = dispatch => ({
    getinputnames: (e) => dispatch(e),
    getinputalias: (e) => dispatch(e),
    getinputtitle: (e) => dispatch(e),
    getinputkeyword: (e) => dispatch(e),
    getinputdescript: (e) => dispatch(e),
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Ubdateinput);