import React from 'react';
import Inserticon from '../components/Inserticon';
import Deleteicon from '../components/Deleteicon';
function MenuList(props) {

    return (

        <thead className="thead-light">

            <tr>
                <th style={{ width: '100px' }}></th>
                <th colSpan="5">
                    <div className="row">
                        <div className="col-2">
                            <h3>{props.header}</h3>
                        </div>
                        <div className=" col-1">
                            <div>
                                <Inserticon alias={props.alias} title={props.title} />
                            </div>
                        </div>
                        <div className="col-1">
                            <div onClick={() => { props.getstateupdate(props.arrcheceds);  props.delete(props.arrcheceds.join(','));}}>
                                <Deleteicon title={props.deletetitle} />
                            </div>
                        </div>
                        <div className="col">
                            {props.err}
                        </div>
                    </div>
                </th>
            </tr>
        </thead>

    )
} export default MenuList;