import React, { useState, useEffect } from 'react';
import Trafic from '../components/Trafic';
import Application from '../components/Application';
import { array_obj } from '../actions';
import { connect } from 'react-redux';
import {trafik, deluser, user } from '../redux/actions';
function Admin(props) {
    const [counts, setcounts] = useState(13);
    const [rad, setrad] = useState(7);
    const [trafikB, settrafikB] = useState('month');
    const [trafikM, settrafikM] = useState('monthM');
    const [chekeds, setcheked] = useState(false);
    const [chekedsch, setchekedch] = useState({ b: false, k: [] });
    const [deleteusers, setdeleteusers] = useState([]);
    const weeks = props.weekday;

    useEffect(() => {
        props.gettrafik();
    }, []);

    useEffect(() => {
        props.getdeleteuser(deleteusers);
        props.getusers();
    }, [deleteusers, props.userd]);

    function weekD(arr = [], f) {
        let a = [];
        arr.map((x, i) => {
            a[i] = f(x)
        }
        )
        return a;
    }

    function convertY(n) {
        n = Number(n);
        let r = [];
        let g = [];
        let h = 401;
        for (let i = 0; i < 401; i++) {
            h = h - 1;
            r[i] = i;
            g[i] = h;

        }

        return g[r.indexOf(n)];
    }

    let winWidth = window.innerWidth;
    let del = winWidth / counts;
    let n = del;
    
    function pointsArray(n, arr) {
        let a = [];
        let d = -n;
        for (let i = 0; i < arr.length; i++) {
            d = d + n
            a[i] = [d, arr[i]]

        }
        return a;
    }


    function isnumarraysiteconvert(array) {
        let ts = [];
        array_obj(array).map((g, i) => {
            ts[i] = convertY(g.trafik_site);
        })
        return ts;
    }

    function trafikWeekJoinArray(array) {
        let t1 = [];
        let t2 = [];
        let t3 = [];
        let t4 = [];
        array_obj(array).map((g, i) => {
            t1[i] = convertY(g.trafik_week_1);
        });
        array_obj(array).map((g, i) => {
            t2[i] = convertY(g.trafik_week_2);
        });
        array_obj(array).map((g, i) => {
            t3[i] = convertY(g.trafik_week_3);
        });
        array_obj(array).map((g, i) => {
            t4[i] = convertY(g.trafik_week_4);
        });
        return t1.concat(t2, t3, t4);
    }

    function trafikDayJoinArray(array) {
        let t1 = [];

        array_obj(array).map((g, i) => {
            t1 = t1.concat(
                convertY(g.trafik_dey_1),
                convertY(g.trafik_dey_2),
                convertY(g.trafik_dey_3),
                convertY(g.trafik_dey_4),
                convertY(g.trafik_dey_5),
                convertY(g.trafik_dey_6),
                convertY(g.trafik_dey_7),
                convertY(g.trafik_dey_8),
                convertY(g.trafik_dey_9),
                convertY(g.trafik_dey_10),
                convertY(g.trafik_dey_11),
                convertY(g.trafik_dey_12),
                convertY(g.trafik_dey_13),
                convertY(g.trafik_dey_14),
                convertY(g.trafik_dey_15),
                convertY(g.trafik_dey_16),
                convertY(g.trafik_dey_17),
                convertY(g.trafik_dey_18),
                convertY(g.trafik_dey_19),
                convertY(g.trafik_dey_20),
                convertY(g.trafik_dey_21),
                convertY(g.trafik_dey_22),
                convertY(g.trafik_dey_23),
                convertY(g.trafik_dey_24),
                convertY(g.trafik_dey_25),
                convertY(g.trafik_dey_26),
                convertY(g.trafik_dey_27),
                convertY(g.trafik_dey_28),
                convertY(g.trafik_dey_29),
                convertY(g.trafik_dey_30),
            );
        });
        return t1;


    }
    trafikDayJoinArray(props.trafik);
    return (
        <div className="container-fluid" >
            
            <div className="row">
                <div className="trafik_bottom col-1" onClick={() => { settrafikB('month'); setcounts(13); setrad(7) }} onMouseOver={() => settrafikM('monthM')} style={(trafikM === 'monthM') ? { backgroundColor: '#edeff2' } : { backgroundColor: '#d5e1ee' }}>
                    Месяц
               </div>
                <div className="trafik_bottom col-1" onClick={() => { settrafikB('week'); setcounts(55); setrad(5) }} onMouseOver={() => settrafikM('weekM')} style={(trafikM === 'weekM') ? { backgroundColor: '#edeff2' } : { backgroundColor: '#d5e1ee' }}>
                    Неделя
               </div>
                <div className="trafik_bottom col-1" onClick={() => { settrafikB('day'); setcounts(400); setrad(2) }} onMouseOver={() => settrafikM('dayM')} style={(trafikM === 'dayM') ? { backgroundColor: '#edeff2' } : { backgroundColor: '#d5e1ee' }}>
                    День
               </div>
            </div>

            <Trafic pointsArray={pointsArray} arr2={isnumarraysiteconvert} n={n} trafik={props.trafik} trafikWeekJoinArray={trafikWeekJoinArray} trafikDayJoinArray={trafikDayJoinArray} rad={rad} trafikB={trafikB} />
            <div className="row mt-3">
                <div className="col-sm ">
                    <Application users={props.users} array_obj={array_obj} setcheked={setcheked} chekeds={chekeds} chekedsch={chekedsch} setchekedch={setchekedch} setdeleteusers={setdeleteusers} />
                </div>

            </div>
            <div></div>



        </div>


    )
}

const setPath = state => ({
    trafik: state.trsfik,
    userd: state.userd,
    users: state.users

})

const toProps = dispatch => ({
    gettrafik: () => dispatch(trafik()),
    getdeleteuser: (id) => dispatch(deluser(id)),
    getusers: () => dispatch(user())
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Admin);
