import React, { useEffect } from 'react';
import {array_obj, initalBaseUrl} from '../actions';
import Updatelist from '../components/Updatelist';
import List from '../components/List';
import '../css/artmain.css'
import MenuList from '../components/MenuList';
import { connect } from 'react-redux';
import { art_a, artid_a, menuartid_a, deleteart_a } from '../redux/actions';
function Articlesmain(props) {
    useEffect(() => {
        props.getarticles();
    }, []);

    useEffect(() => {
        props.getremoveart(props.articles, props.stateupdate.m);
    }, [props.articles, props.stateupdate.m]);
    useEffect(() => {
        props.getartid(props.match.params.id);
    }, [props.match.params.id]);

    return (
        <div className="container-fluid p-0" >

            <div className="row">
                <div className="col-sm">
                    <table className="table table-light">
                        <MenuList getstateupdate={props.getstateupdate} arrcheceds={props.arrcheceds} delete={props.getartdelete} header={'Статьи'} alias={'/admin/index.php/articlesinsert'} title={'Добавить статью'} deletetitle={'Удалить статью'} />
                        <tbody>
                            {
                                array_obj(props.removeart).map((x, i) =>
                                    <List key={x.art_id + 5} arrcheceds={props.arrcheceds} getarrcheceds={props.getarrcheceds} id={x.art_id} lin={`/admin/index.php/articles/${x.art_id}`} name={x.art_name} />
                                )
                            }
                        </tbody>
                    </table>
                </div>
                <div className="col-sm">
                    <Updatelist
                        updatealias={`/admin/index.php/articlesupdate/${props.articlesid.art_id}`}
                        a={{
                            name:
                                props.articlesid.art_name,
                            alias: props.articlesid.art_alias,
                            title: props.articlesid.art_title,
                            keyword: props.articlesid.art_keyword,
                            descript: props.articlesid.art_descript, 
                            subcontent: props.articlesid.art_subcontent, 
                            content: props.articlesid.art_content, 
                            img: props.articlesid.art_img
                        }} initalBaseUrl={initalBaseUrl} />
                </div>
            </div>

        </div>
    )
}
const setPath = state => ({
    articles: state.articles,
    articlesid: state.articlesid,
    menuartid: state.menuartid,
    arrcheceds: state.arrcheceds,
    deleteart: state.deleteart,
    menuartidin: state.menuartidin,
    stateupdate: state.stateupdate,
    removeart: state.removeart,



})

const toProps = dispatch => ({
    getarticles: () => dispatch(art_a()),
    getartid: (id) => dispatch(artid_a(id)),
    getarticlesid: (id) => dispatch(menuartid_a(id)),
    getarrcheceds: (ars, e, f) => dispatch({ type: 'ARRCHECEDS', ars: ars, e: e, f: f }),
    getartdelete: (id) => dispatch(deleteart_a(id)),
    getstateupdate: (id) => dispatch({ type: 'STATEUPDATE', payload: id }),
    getremoveart: (menu, id) => dispatch({ type: 'REMOVE_ART', menu: menu, id: id }),

})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Articlesmain);