import React from 'react';
import { Link } from 'react-router-dom';
import { array_obj } from '../actions';
function Trafic(props) {
    let a = -props.n;
    let d = new Date();
    let y = d.getFullYear();
    let trafiklist = props.arr2(array_obj(props.trafik));
    if (props.trafikB === 'month'){
        trafiklist = props.arr2(array_obj(props.trafik));
    } else if (props.trafikB === 'week'){
        trafiklist = props.trafikWeekJoinArray(props.trafik);
    } else if (props.trafikB === 'day') {
        trafiklist = props.trafikDayJoinArray(props.trafik);
    }
    return (
        <div >
        <h4 className = "text-center mt-2">Посещаемость сайта на <span>{y}</span> год</h4>
            <svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" height="400">
                <polyline points={props.pointsArray(props.n, trafiklist)}
                    style={{ fill: 'none', stroke: '#19c7d9', strokeWidth: 3 }} />
                {trafiklist.map((x, i) => <circle key = {i + 10} cx={a = a + props.n} cy={x} r={props.rad} stroke="#19c7d9" fill= "#19c7d9"/>)}
                
            </svg>
        
            <div className="row ">
                {array_obj(props.trafik).map((x, i) => <div key={i + 2} className="col-sm-1">{x.trafik_month}</div>)
                  
                }

            </div>
            
        </div>

    )
}
export default Trafic;