import React from 'react';
import { Link } from 'react-router-dom';
import '../css/main.css';
import ChecedsClick from './ChecedsClick';
import Deleteicon from './Deleteicon';
function Application(props) {
    return (
        <div>
            <div className="row">
                <div className="col-1">
                    <h4>Заявки</h4>
                </div>

            </div>

            <table className="table table-light">
                <thead>
                    <tr>
                        <td className="ch" onClick={() => {
                           
                            let j = [];
                            if (props.chekeds === false) {
                                props.setcheked(true);
                                props.users.map((x, i) => {
                                    j[i] = x.userM_id;
                                })
                                props.setchekedch({ b: true, k: j });
                            } else {
                                props.setcheked(false);
                                props.setchekedch({ b: false, k: [] });
                            }

                        }}>
                            <ChecedsClick c={props.chekeds} />
                        </td>
                        <td>
                            <div className="dl" title="Удалить" onClick={() => props.setdeleteusers(props.chekedsch.k)}>
                                {(props.chekedsch.k.length > 0) ?
                                    <Deleteicon />
                                    : ''}

                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td><b>Имя</b></td>
                        <td>Телефон</td>
                        <td>Почта</td>
                        <td>Заявка</td>
                        <td>Браузер</td>
                        <td>ip</td>
                        <td>Дата</td>
                        <td>Время</td>
                    </tr>
                </thead>
                <tbody>
                    {props.users.map((x) =>
                        <tr key={x.userM_id + 10}>
                            <td className="ch"
                                onClick={() => {
                                    let c = props.chekedsch.k;
                                    if (c.indexOf(x.userM_id) === -1) {
                                        c.push(x.userM_id)
                                        props.setchekedch({ b: true, k: c });
                                    } else {
                                        c.splice(c.indexOf(x.userM_id), 1)
                                        props.setchekedch({ b: false, k: c });
                                    }

                                }}
                            >
                                <ChecedsClick c={props.chekedsch.k[props.chekedsch.k.indexOf(x.userM_id)]} />


                            </td>
                            <td>
                                {x.userM_name}
                            </td>
                            <td>
                                {x.userM_tel}
                            </td>
                            <td>
                                {x.userM_mail}
                            </td>
                            <td>
                                {x.userM_tema}<br />{x.userM_content}
                            </td>
                            <td>
                                {x.userM_brouser}
                            </td>
                            <td>
                                {x.userM_ip}
                            </td>
                            <td>
                                {x.userM_data}
                            </td>
                            <td>
                                {x.userM_time}
                            </td>
                        </tr>
                    )}
                </tbody>
            </table>



        </div>

    )
}
export default Application;