import React from 'react';
import ChecedsClick from './ChecedsClick';
import Deleteicon from './Deleteicon';

function Articles(props) {
    return (
        <div className="col-sm mt-3">
            <div className = "row"><div className = "col-2"><h3>Статьи</h3></div><div className = "col"><Deleteicon title = "Удалить статью"/></div></div>
            {props.menuartid.map((x) => <div key={x.art_name + 5} onClick={() => { props.getarrcheceds(props.arrcheceds, x.art_id, { b: true, k: props.arrcheceds }) }}><div className = "row"><div className = "col-0 p-0"><ChecedsClick c={props.arrcheceds[props.arrcheceds.indexOf(x.art_id)]}/></div><div className = "col">{x.art_name}</div></div></div>)}
            <div className="col"><h3>Привязать статью</h3></div>
            {props.articles.map((x, i) => (props.menuartid[i] === undefined)?<div key={x.art_name + 5} onClick={() => { props.getarrcheceds(props.arrcheceds, x.art_id, { b: true, k: props.arrcheceds }) }}><div className="row"><div className="col-0 p-0"><ChecedsClick c={props.arrcheceds[props.arrcheceds.indexOf(x.art_id)]} /></div><div className="col">{x.art_name}</div></div></div>:'')}

        </div>
    )
} export default Articles;