import React from 'react';
import axois from 'axios';
import { NavLink } from "react-router-dom";
import gets from "../db/gets.json"

export const initalBaseUrl = gets.getbaseurl;

export const array_obj = (arr) => {
    let new_arr = [];
    // eslint-disable-next-line
    for (const key in arr) {

        new_arr[key] = arr[key];


    }
    return new_arr;
}


export function getPrais(f, profiles, okno, stvorka) {
    axois.get(initalBaseUrl + gets.getPrais, {
        params: {
            profiles: profiles,
            okno: okno,
            stvorka: stvorka
        }
    })
        .then(function (response) {
            f({ data: response.data, status: response.status });
        })
}

