export function replaceUrls(str = '') {
    str = str.replace(/Главная/, '/');
    (str === '/') ? str = str : str = `${str}.html`;
    str = str.replace(/ /g, '_');
    str = str.replace(/\-/g, '_');
    str = str.replace(/\./g, '.');
    str = str.split('');
    let arr_ru = ['.', '/', '_', 'А', 'а', 'Б', 'б', 'В', 'в', 'Г', 'г', 'Д', 'д', 'Е', 'е', 'Ё', 'ё', 'Ж', 'ж', 'З', 'з', 'И', 'и', 'Й', 'й', 'К', 'к', 'Л', 'л', 'М', 'м', 'Н', 'н', 'О', 'о', 'П', 'п', 'Р', 'р', 'С', 'с', 'Т', 'т', 'У', 'у', 'Ф', 'ф', 'Х', 'х', 'Ц', 'ц', 'Ч', 'ч', 'Ш', 'ш', 'Щ', 'щ', 'Э', 'э', 'Ы', 'ы', 'Ю', 'ю', 'Я', 'я'];
    let arr_en = ['.', '/', '_', 'A', 'a', 'B', 'b', 'V', 'v', 'G', 'g', 'D', 'd', 'E', 'e', 'E', 'e', 'J', 'j', 'Z', 'z', 'I', 'i', 'I', 'i', 'K', 'k', 'L', 'l', 'M', 'm', 'N', 'n', 'O', 'o', 'P', 'p', 'R', 'r', 'S', 's', 'T', 't', 'U', 'u', 'F', 'f', 'H', 'h', 'C', 'c', 'C', 'c', 'H', 'h', 'H', 'h', 'E', 'e', 'I', 'i', 'Y', 'y', 'I', 'i'];
    str = str.map((x) => (arr_en[arr_ru.indexOf(x)] === undefined) ? arr_en[arr_en.indexOf(x)] : arr_en[arr_ru.indexOf(x)]);
    str = str.join('');
    return str;
}

export function arriteger(e, ars, f) {
    let c = ars.concat();

    if (c.indexOf(e) === -1) {
        c.push(e);
        f({ b: true, k: c });
    } else {
        c.splice(c.indexOf(e), 1)
        f({ b: false, k: c });
    }
    return c;
}

