import React, { useState, useEffect } from 'react';
import '../css/menutether.css';
const Menutether = (props) => {
    const [list, setlist] = useState('list');
    const [names, setnames] = useState('');
    const [id, setid] = useState(0);

    useEffect(() => {

        props.menu.map((x) => (x.menu_id === props.menuid.parent) ? setid(x.menu_id) : '');
        props.menu.map((x) => (x.menu_id === props.menuid.parent) ? setnames(x.menu_name) : '');
        if (id === undefined) {
            setid(0)
        }
    }, [props.menuid.menu_id]);
    return (
        <div className="col" className="menutether form-group ">

            <label htmlFor="pr"><h3>Привязать к пункту меню</h3></label>
            <input id="pr" className="form-control col-8" type="text" defaultValue={names} onClick={() => { setlist('listblock')}} />
            <div className = {list}>
                <div onClick={() => { setnames(''); setlist('list'); setid(0) }}>Нет</div>
                {props.menu.map((x) => (x.parent !== props.menuid.parent) ? <div key={x.menu_id + 5} onClick={() => { setnames(x.menu_name); setlist('list'); setid(x.menu_id) }} >{x.menu_name}</div>:'')}
            </div>
        </div>)
}
export default Menutether;