import { combineReducers } from 'redux'



const trsfik = (state = [{}], action) => {
    switch (action.type) {
        case 'TRAFIK':
            state = { ...state, data: action.payload.data };
            return state.data;
        default:
            return state
    }
}


const configMenu = (state = [{}], action) => {
    switch (action.type) {
        case 'CONFIG_MENU':
            state = { ...state, data: action.payload.data.menu };
            return state.data;
        default:
            return state
    }
}

