const menu = (state = [{}], action) => {
    switch (action.type) {
        case 'MENU':
            state = { ...state, data: action.payload.data };
            return state.data;

        default:
            return state
    }
}

const removemenu = (state = [{}], action) => {
    switch (action.type) {
        case 'REMOVE_MENU':
            let a = [];
            if (action.id === undefined) { action.id = [] }
            if (action.menu === undefined) { action.menu = [{}] }
            let ah = action.id.concat();
            action.menu.map((x, i) =>
                (ah[ah.indexOf(x.menu_id)] === undefined) ? a[i] = { menu_id: x.menu_id, menu_art_id: x.menu_art_id, menu_name: x.menu_name } : {}
            )
            return a.concat();
        default:
            return state;
    }
}

const removeart = (state = [{}], action) => {
    switch (action.type) {
        case 'REMOVE_ART':
            let b = [];
            if (action.id === undefined) { action.id = [] }
            if (action.menu === undefined) { action.menu = [{}] }
            let bh = action.id.concat();
            action.menu.map((x, i) =>
                (bh[bh.indexOf(x.art_id)] === undefined) ? b[i] = { art_id: x.art_id, art_name: x.art_name } : {}
            )
            return b.concat();
        default:
            return state;
    }
}



