import React, {useEffect } from 'react';
import { initalBaseUrl} from '../actions';
import Updatelist from '../components/Updatelist';
import '../css/main.css';
import MenuList from '../components/MenuList';
import List from '../components/List';
import { connect } from 'react-redux';
import { menu_a, menuid_a, menuartid_a, deletemenu_a } from '../redux/actions';
function Main(props) {

    useEffect(() => {
        props.getmenu();
    }, []);
    useEffect(() => {
        props.getmenuid(props.match.params.id);
        props.getmenuartid(props.match.params.id);
    }, [props.match.params.id]);
    useEffect(() => {
        props.getremovemenu(props.menu, props.stateupdate.m);
    }, [props.menu, props.stateupdate.m]);

    
    return (
        <div className="container-fluid p-0" >
            <div className="row">
                <div className="col-sm">
                    <table className="table table-light">
                        <MenuList getstateupdate={props.getstateupdate} arrcheceds={props.arrcheceds} delete={props.getmenudelete} header={'Меню'} alias={`/admin/index.php/menuinsert`} title={'Добавить пункт меню'} deletetitle={'Удалить пункт меню'} />
                        <tbody>
                            {

                                props.removemenu.map((x) =>
                                    <List key={x.menu_id + 5} arrcheceds={props.arrcheceds} getarrcheceds={props.getarrcheceds} id={x.menu_id} lin={`/admin/index.php/menu/${x.menu_art_id}`} name={x.menu_name} />
                                )
                            }
                        </tbody>
                    </table>

                </div>
                <div className="col">
                    <Updatelist
                        
                        updatealias={`/admin/index.php/menuupdate/${props.menuid.menu_art_id}`}
                        a={{ name: props.menuid.menu_name, 
                            alias: props.menuid.menu_alias, 
                            title: props.menuid.menu_title, 
                            keyword: props.menuid.menu_keyword, 
                            descript: props.menuid.menu_descript, 
                            content: props.menuid.menu_content, 
                            img: props.menuid.menu_img }}
                        initalBaseUrl={initalBaseUrl}
                    />

                    <div className="row col mt-3">
                        <div className="col-3">
                            <h5>Статьи</h5>
                        </div>
                        <div className="col">
                            <ul>
                                {props.menuartid.map((x, i) =>
                                    <li key={i + 25} style={{ listStyle: 'disclosure-closed' }}>{x.art_name}</li>
                                )}
                            </ul>

                        </div>
                    </div>

                </div>
            </div>
        </div>


    )
}

const setPath = state => ({
    menu: state.menu,
    menuid: state.menuid,
    menuartid: state.menuartid,
    arrcheceds: state.arrcheceds,
    deletemenu: state.deletemenu,
    menuartidin: state.menuartidin,
    stateupdate: state.stateupdate,
    removemenu: state.removemenu,
    menumessage: state.menumessage,
   

})

const toProps = dispatch => ({
    getmenu: () => dispatch(menu_a()),
    getmenuid: (id) => dispatch(menuid_a(id)),
    getmenuartid: (id) => dispatch(menuartid_a(id)),
    getarrcheceds: (ars, e, f) => dispatch({ type: 'ARRCHECEDS', ars: ars, e: e, f: f }),
    getmenudelete: (id) => dispatch(deletemenu_a(id)),
    getstateupdate: (id) => dispatch({ type: 'STATEUPDATE', payload: id }),
    getremovemenu: (menu, id) => dispatch({ type: 'REMOVE_MENU', menu: menu, id: id }),
   
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Main);
