import React from 'react';
import { Link } from 'react-router-dom';
function Updatelist(props) {
    return (
        <div className="col-sm">
            <div className="text-right mt-3">
                <Link to={props.updatealias} type="button" className="btn btn-primary" data-toggle="modal" data-target="#exampleModal">Редактировать</Link>
            </div>            
            <div className="row mt-3">
                <div className="col-3">
                    <h5>Url адрес</h5>
                        </div>
                <div className="col">
                    {props.a.alias}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    <h5>Заголовок страницы</h5>
                        </div>
                <div className="col">
                    {props.a.title}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3 ">
                    <h5>Ключевые слова</h5>
                        </div>
                <div className="col">
                    {props.a.keyword}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    <h5>Краткое описание страницы</h5>
                        </div>
                <div className="col">
                    {props.a.descript}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    <h5> Краткое описание статьи</h5>
                </div>
                <div className="col">
                    {props.a.subcontent}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    <h5> Текст</h5>
                        </div>
                <div className="col">
                    {props.a.content}
                </div>
            </div>
            {(props.a.img) ?
                <div className="row mt-3">
                    <div className="col-3">
                        <h5>Изображение</h5>
                        </div>
                    <div className="col">
                        <img className="img-fluid" src={`${props.initalBaseUrl}/img/window/${props.a.img}`} alt={props.a.name} />
                    </div>
                </div>
                : <div></div>
            }

        </div>
    )
} export default Updatelist;